@import "components/header";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-3{
          .asside{
            margin: 0 0 30px;
            &__vhLogo{
              height: 105px;
              display: flex;
              flex-direction: column;
              justify-content: center;
              background-image: url("../img/vh_logo.jpg");
              background-size: cover;
              p{
                padding: 0 0 0 130px;
                font: 2.438em/.875em "BravoSC";
                color: #d10000;
                &:nth-child(1){
                  font: 1.625em/.875em "BravoSC";
                  color: #d10000;
                }
              }
            }
            &__menu{
              margin: 30px 0;
              ul{
                li{
                  a{
                    padding: 0 0 0 20px;
                    display: block;
                    font: .938em/30px "Arial";
                    font-weight: bold;
                    color: #0081ef;
                    text-decoration: none;
                    border: 1px solid #c4c4c4;
                    &:hover{
                      background-color: #d1efff;
                    }
                  }
                }
              }
            }
            &__bestHolo,
            &__newHolo,
            &__threeD{
              margin: 30px 0;
              border: 1px solid #c4c4c4;
              a{
                display: block;
                text-decoration: none;
                h2{
                  padding: 0 0 0 15px;
                  font: .938em/40px "Arial";
                  font-weight: bold;
                  color: #777;
                  text-transform: uppercase;
                  background-color: #eee;
                  border-bottom: 1px solid #c4c4c4;
                }
                span{
                  display: block;
                  padding: 20px 15px;
                  font: .875em/1.25em "Arial";
                  font-weight: bold;
                  color: #0081ef;
                  text-transform: uppercase;
                }
                img{
                  width: 100%;
                  padding: 15px;
                }
              }
            }
            &__examples{
              margin: 30px 0;
              border: 1px solid #c4c4c4;
              h2{
                padding: 0 0 0 15px;
                font: .938em/40px "Arial";
                font-weight: bold;
                color: #777;
                text-transform: uppercase;
                background-color: #eee;
                border-bottom: 1px solid #c4c4c4;
              }
              ul{
                li{
                  display: flex;
                  flex-wrap: wrap;
                  a{
                    width: 50%;
                    display: inline-block;
                    img{
                      width: 100%;
                      padding: 10px;
                    }
                  }
                }
              }
              .controls{
                display: flex;
                .prev,
                .next{
                  width: 50%;
                  display: flex;
                  justify-content: space-around;
                  align-items: center;
                  font: .875em/40px "Arial";
                  color: #0081ef;
                  text-align: center;
                  background-color: #eee;
                  border-top: 1px solid #c4c4c4;
                  .dot{
                    font-size: .4em;
                    color: #777;
                  }
                  &:hover{
                    cursor: pointer;
                  }
                }
              }
            }
            &__custom{
              margin: 30px 0;
              border: 1px solid #c4c4c4;
              h2{
                padding: 10px 0 10px 15px;
                font: .938em/1.25em "Arial";
                font-weight: bold!important;
                color: #0081ef;
                text-transform: uppercase;
                background-color: #eee;
                border-bottom: 1px solid #c4c4c4;
              }
              p{
                padding: 10px 15px;
                font: .875em/1em "Arial";
                color: #777;
                a{
                  color: #0081ef;
                }
              }
            }
          }
        }
        .col-lg-9{
          .products{
            &__compare{
              display: flex;
              justify-content: flex-end;
              a{
                padding: 0 25px 0 0;
                display: block;
                font: .875em/3em "Arial";
                color: #0081ef;
                text-decoration: none;
              }
            }
            &__sortOptions{
              display: flex;
              justify-content: space-between;
              .filter{
                display: flex;
                align-items: center;
                p{
                  font: 1em/1.125em "Arial";
                  font-weight: bold;
                  color: #777;
                }
                select{
                  margin: 0 15px;
                  padding: 0 10px;
                  height: 25px;
                  font: .938em/25px "Arial";
                  color: #4c4c4c;
                  background-color: #eaeaea;
                  outline: none;
                }
              }
              .sortBy{
                display: flex;
                .sort,
                .quantitu{
                  display: flex;
                  align-items: center;
                  p{
                    font: 1em/1.125em "Arial";
                    font-weight: bold;
                    color: #777;
                  }
                  select{
                    margin: 0 15px;
                    padding: 0 10px;
                    height: 25px;
                    font: .938em/25px "Arial";
                    color: #4c4c4c;
                    background-color: #eaeaea;
                    outline: none;
                  }
                }
              }
            }
            &__cart{
              margin: 15px 0;
              width: 100%;
              display: flex;
              flex-wrap: wrap;
              justify-content: space-between;
              .cart{
                margin: 15px 0;
                width: 30%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: relative;
                border: 1px solid #dfdfdf;
                a{
                  display: block;
                  text-decoration: none;
                  img{
                    width: 100%;
                    padding: 15px 20px;
                  }
                  span{
                    display: block;
                    padding: 15px 20px;
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    color: #0081ef;
                  }
                }
                ul{
                  display: flex;
                  li{
                    a{
                      display: inline-block;
                      font: .875em/40px "Arial";
                      font-weight: bold;
                      color: #777;
                      text-align: center;
                      background-color: #eee;
                      border-top: 1px solid #dfdfdf;
                    }
                    .inCart{
                      width: 151.9px;
                    }
                    .heart,
                    .exchange{
                      width: 50px;
                      border-left: 1px solid #dfdfdf;
                    }
                  }
                }
                .hover{
                  display: none;
                  width: 254.25px;
                  position: absolute;
                  bottom: -150px;
                  left: -1px;
                  background-color: #fff;
                  border: 1px solid #78afeb;
                  border-top: none;
                  box-shadow: 5px 5px 15px #cecece;
                  z-index: 5;
                  .descr{
                    padding: 10px 20px;
                    display: flex;
                    flex-direction: column;
                    li{
                      p{
                        font: .875em/1.375em "Arial";
                        font-weight: bold;
                        span{
                          padding: 0 0 0 5px;
                          font-weight: normal;
                        }
                      }
                    }
                  }
                  .about{
                    padding: 0 20px 15px;
                    font: .875em/1em "Arial";
                    color: #636363;
                  }
                }
                &:hover{
                  border-color: #78afeb;
                  border-bottom-color: #dfdfdf;
                  box-shadow: 5px 5px 15px #cecece;
                  .hover{
                    display: block;
                  }
                }
              }
            }
            &__pagination{
              display: flex;
              justify-content: center;
              align-items: flex-end;
              ul{
                display: flex;
                justify-content: center;
                li{
                  margin: 0 5px;
                  width: 45px;
                  height: 40px;
                  background-color: #efefef;
                  border: 1px solid #dedede;
                  span{
                    display: block;
                    font: 1.438em/40px "Arial";
                    font-weight: bold;
                    color: #0081ef;
                    text-align: center;
                    i{
                      font-weight: bold;
                    }
                    &:hover{
                      cursor: pointer;
                    }
                  }
                }
                .active{
                  background-color: #fff;
                  border-color: #0081ef;
                }
              }
            }
          }
        }
      }
    }
  }
}