body{
  font-family: Arial, serif;
  font-size: 16px;
  line-height: 18px;
  .header {
    &__nav{
      .container {
        .row {
          .col-lg-1 {
            ul{
              li{
                a {
                  display: flex;
                  align-items: center;
                  position: relative;
                  font: .875em/50px "Arial";
                  color: #777a79;
                  text-decoration: none;
                  z-index: 10;
                }
                ul {
                  display: none;
                  flex-direction: column;
                  position: absolute;
                  top: 40px;
                  background: #fff;
                  z-index: 10;
                  li{
                    a{
                      line-height: 30px;
                    }
                  }
                }
                &:hover ul{
                  display: flex;
                }
              }
            }
          }
          .col-lg-5 {
            ul {
              display: flex;
              justify-content: space-between;
              li {
                a {
                  display: flex;
                  align-items: center;
                  position: relative;
                  font: .875em/50px "Arial";
                  color: #777a79;
                  text-decoration: none;
                }
              }
            }
          }
        }
      }
    }
    &__headline{
      height: 135px;
      background-color: #e6e6e6;
      .container{
        .row{
          p{
            font: 8.9em/135px "BravoSC";
            color: #eaeaea;
            text-align: center;
            position: relative;
          }
          .col-lg-6{
            .logo{
              height: 135px;
              display: flex;
              justify-content: center;
              align-items: center;
              position: absolute;
              top: -135px;
              z-index: 5;
              div{
                position: relative;
                left: -15px;
                p{
                  &:nth-child(1){
                    margin: 0 0 0 20px;
                    font: 1.188em/1em "BravoSC";
                    color: #00548f;
                    text-align: left;
                  }
                  &:nth-child(2){
                    font: 1.5em/1em "BravoSC";
                    color: #00548f;
                    text-align: left;
                  }
                }
              }
            }
            .search{
              width: 95%;
              height: 135px;
              display: flex;
              justify-content: flex-end;
              align-items: center;
              position: absolute;
              top: -135px;
              z-index: 5;
              input{
                width: 460px;
                height: 33px;
                padding: 0 0 0 15px;
                font: .875em/33px "Arial";
                color: #9fa1a2;
                background-color: #f5f5f5;
                border: 1px solid #c4c4c4;
              }
              i{
                width: 37px;
                height: 33px;
                line-height: 33px;
                text-align: center;
                background: #fff;
                border-top: 1px solid #c4c4c4;
                border-right: 1px solid #c4c4c4;
                border-bottom: 1px solid #c4c4c4;
              }
            }
          }
        }
      }
    }
  }
  .header__mainNav{
    background-color: #009fff;
    .container{
      .row{
        .col-lg-12{
          ul{
            display: flex;
            justify-content: space-between;
            li{
              position: relative;
              a{
                height: 60px;
                padding: 0 5px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                position: relative;
                text-decoration: none;
                span{
                  display: block;
                  text-align: center;
                  text-transform: uppercase;
                  &:nth-child(1){
                    font: .938em/1.063em "Arial";
                    font-weight: bold;
                    color: #fff;
                  }
                  &:nth-child(2){
                    font: .938em/1.063em "Arial";
                    color: #9fdbff;
                  }
                }
                &:after{
                  content: "";
                  width: 100%;
                  height: 0;
                  position: absolute;
                  top: 100%;
                  left: 0;
                  border: 15px solid transparent;
                }
                &:hover{
                  background-color: #007eec;
                  &:after{
                    border: 90px solid transparent;
                    border-top: 18px solid #007eec;
                  }
                }
              }
            }
            .active{
              background-color: #007eec;
              &:after{
                content: "";
                width: 100%;
                height: 0;
                position: absolute;
                top: 100%;
                left: 0;
                border: 90px solid transparent;
                border-top: 18px solid #007eec;
              }
            }
          }
        }
      }
    }
  }
}